import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import {Modal, Button, Form, Col, FormControl, FormGroup, ControlLabel} from 'react-bootstrap';

import icon from '../assets/img/logo-icon-i.png';

class Navbar extends Component {

    constructor(props, context) {
        super(props, context);
        this.state = {
            showModal: false
        };

        this.getInitialState = this.getInitialState.bind(this);
        this.close = this.close.bind(this);
        this.open = this.open.bind(this);

    }

    getInitialState() {
        return { showModal: false };
    }

    close() {
        this.setState({ showModal: false });
    }

    open() {
        this.setState({ showModal: true });
    }

    render() {
        return (
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
                <div className="container">
                    <Link to="/garajes" className="navbar-brand">
                        <img className="navbar-icon" src={icon} alt=""/>
                    </Link>
                    <button className="navbar-toggler" type="button" data-toggle="collapse"
                            data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false"
                            aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"/>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarResponsive">
                        <ul className="navbar-nav ml-auto">
                            <li className={this.props.current === 'Inicio' ? "nav-item active" : "nav-item"}>
                                <Link to="/garajes" className="nav-link">
                                    Inicio
                                </Link>
                            </li>
                            <li className={this.props.current === 'Acerca' ? "nav-item active" : "nav-item"}>
                                <Link to="/acerca" className="nav-link">
                                    Acerca
                                </Link>
                            </li>
                            <li className={this.props.current === 'Contacto' ? "nav-item active" : "nav-item"}>
                                <Link to="/contacto" className="nav-link">
                                    Contacto
                                </Link>
                            </li>
                            <li className={this.props.current === 'Ingresar' ? "nav-item active" : "nav-item"}>
                                <span className="nav-link" onClick={this.open}>
                                    Ingresar
                                </span>
                            </li>
                        </ul>
                    </div>
                </div>
                <Modal show={this.state.showModal} onHide={this.close}>
                    <Form horizontal>
                        <Modal.Header closeButton>
                            <Modal.Title>Ingresar</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                                <FormGroup controlId="email">
                                    <Col componentClass={ControlLabel}>
                                        Email
                                    </Col>
                                    <Col>
                                        <FormControl type="email" />
                                    </Col>
                                </FormGroup>

                                <FormGroup controlId="password">
                                    <Col componentClass={ControlLabel}>
                                        Contraseña
                                    </Col>
                                    <Col>
                                        <FormControl type="password" />
                                    </Col>
                                </FormGroup>
                        </Modal.Body>
                        <Modal.Footer>
                            <Button onClick={this.close} className="btn btn-danger">Cerrar</Button>
                            <Button type="submit" className="btn btn-success"> Entrar</Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </nav>
        );
    }
}

export default Navbar;



