import React, { Component } from 'react';

import {Form, FormGroup, Col, FormControl, Button, ControlLabel} from 'react-bootstrap';

import Navbar from '../components/Navbar';
import mainPublication from '../assets/img/main-publication.jpg';


class Contact extends Component {
    render() {
        return (
            <div className="contact">
                <Navbar current="Contacto"/>
                <div className="container contact-container">
                    <div className="row text-center">
                        <div className="col-12">
                            <div className="panel with-scroll animated zoomIn card-detail">
                                <div className="panel-body">
                                    <div className="row">
                                        <div className="col-md-4 order-2 order-md-1">
                                            <img className="card-img-top" src={mainPublication} alt=""/>
                                        </div>
                                        <div className="col-md-8 text-justify order-1 order-md-2">
                                            <h1>Contactanos</h1>

                                            <Form horizontal>
                                                <FormGroup controlId="name">
                                                    <Col componentClass={ControlLabel}>
                                                        Nombres y Apellidos
                                                    </Col>
                                                    <Col>
                                                        <FormControl type="text" />
                                                    </Col>
                                                </FormGroup>

                                                <FormGroup controlId="email">
                                                    <Col componentClass={ControlLabel}>
                                                        Email
                                                    </Col>
                                                    <Col>
                                                        <FormControl type="email" />
                                                    </Col>
                                                </FormGroup>

                                                <FormGroup controlId="phone">
                                                    <Col componentClass={ControlLabel}>
                                                        Telefono
                                                    </Col>
                                                    <Col>
                                                        <FormControl type="phone" />
                                                    </Col>
                                                </FormGroup>

                                                <FormGroup controlId="details">
                                                    <Col componentClass={ControlLabel}>
                                                        Detalles
                                                    </Col>
                                                    <Col>
                                                        <FormControl componentClass="textarea" placeholder="Escribe aqui tu inquietud o sugerencia" />
                                                    </Col>
                                                </FormGroup>

                                                <FormGroup>
                                                    <Col>
                                                        <Button type="submit">
                                                            Enviar
                                                        </Button>
                                                    </Col>
                                                </FormGroup>
                                            </Form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Contact;


