import React, { Component } from 'react';
import {Link} from 'react-router-dom';

class Main extends Component {
    render() {
        return (
            <header className="header" id="top">
                <div className="text-vertical-center">
                    <h1>Mi Venta de Garaje</h1>
                    <h3>Encuentra ventas de garaje cerca de ti, o publica la tuya!</h3>
                    <br/>
                    <Link to="/garajes" className="btn btn-lg btn-transparent">Entrar</Link>
                </div>
            </header>
        );
    }
}

export default Main;


