import React, { Component } from 'react';

import Payments from './Payments';
import Condition from './Condition';
import Map from './Map';

import mainPublication from '../assets/img/main-publication.jpg';

class CardDetail extends Component {


    constructor(props, context) {
        super(props, context);
        this.updateProduct = this.updateProduct.bind(this);

        this.state = {
            mainImage: mainPublication,
            price: '',
            condition: '',
            showPrice: false
        };

    }

    updateProduct(image,price,condition) {
        this.setState({
            mainImage: image,
            price: price,
            condition: condition,
            showPrice: true
        });
    }


    render() {

        let dbData = {
            id: 1,
            location: {
                city: 'Merida',
                country: 'Venezuela',
                coords: {
                    lat: 41.4054682,
                    lng: 2.12373473
                }
            },
            description: 'aa',
            products: [
                {
                    image: '1.jpg',
                    price: 1,
                    condition: 4.5
                },
                {
                    image: '2.jpg',
                    price: 2,
                    condition: 5
                },
                {
                    image: '3.jpg',
                    price: 3,
                    condition: 3.5
                },
                {
                    image: '4.jpg',
                    price: 4,
                    condition: 2
                },
                {
                    image: '5.jpg',
                    price: 5,
                    condition: 4
                }
            ],
            payments: [
                'master',
                'visa',
                'mercantil',
                'bbva',
                'banesco',
                'bfc',
                'tesoro',
                'venezuela',
                'bod',
                'bicentenario',
                'exterior',
                'credito'
            ]
        };
        let images = [];

        dbData.products.map(data =>
            images.push(require('../assets/img/products/'+dbData.id+'/'+ data.image))
        );


        return (
            <div className="col-12">
                <div className="panel with-scroll animated zoomIn card-detail">
                    <div className="panel-body">
                        <div className="row">
                            <div className="col-12 col-md-5 col-lg-4 text-center">
                                <div className="image-product-container">
                                    <img src={this.state.mainImage} alt="product" className="img-fluid image-product"/>
                                </div>
                                <div className={this.state.showPrice ? 'row' : 'row hidden'}>
                                    <div className="col-12 col-md-6 payment">
                                        <br/>
                                        <div className="price">Bs. {this.state.price}</div>
                                        <Condition condition={this.state.condition}/>
                                    </div>
                                    <div className="col-12 col-md-6 text-center">
                                        <div className="row">
                                            <div className="col-12">
                                                <br/>
                                                <button type="button" className="btn btn-success">
                                                    Comprar
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <hr/>
                                {
                                    images && images.map((image,i) =>
                                        <img src={image} key={i} alt="product" className="img-thumbnail"
                                             onClick={() => this.updateProduct(image, dbData.products[i].price,dbData.products[i].condition) }/>
                                    )
                                }

                            </div>
                            <div className="col-12 col-md-7 col-lg-8">
                                <div className="row">
                                    <div className="col-12 text-detail">
                                        Lorem Ipsum es simplemente el texto de relleno de las imprentas
                                        y archivos de texto.
                                        Lorem Ipsum ha sido el texto de relleno estándar de las
                                        industrias desde el año 1500,
                                        cuando un impresor (N. del T. persona que se dedica a la
                                        imprenta) desconocido usó
                                        una galería de textos y los mezcló de tal manera que logró hacer
                                        un libro de
                                        textos especimen. No sólo sobrevivió 500 años, sino que tambien
                                        ingresó como
                                        texto de relleno en documentos electrónicos, quedando
                                        esencialmente igual al original.
                                        Fue popularizado en los 60s con la creación de las hojas
                                        "Letraset", las cuales
                                        contenian pasajes de Lorem Ipsum, y más recientemente con
                                        software de autoedición,
                                        como por ejemplo Aldus PageMaker, el cual incluye versiones de
                                        Lorem Ipsum.
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-12">
                                        <Map coords={dbData.location.coords}/>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-12">
                                        {dbData.location.city}, {dbData.location.country}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="panel-footer">
                        <Payments payments={dbData.payments}/>
                    </div>
                </div>
            </div>
        );
    }
}

export default CardDetail;


