import React from 'react';
import paymentsBackground from '../assets/img/payments/logos.png';

const Payments = ({payments}) => {

    return (
        <div className="col-12 payment text-center">
            {payments.map((payment,i) =>
                <span key={i} className={`payments ${payment}-small`}
                      style={{backgroundImage: `url(${paymentsBackground})`}}/>
            )}
        </div>
    );
};

export default Payments;



