import React from 'react';

const Condition = ({condition}) => {

    return (
        condition &&
            <div className="condition" style={{display:'inline-block'}}>
                {[...Array(parseInt(condition,10))].map((x, i) =>
                    <i className="fa fa-star" aria-hidden="true" key={i} />
                )}
                {
                    condition % 1 !== 0 &&
                    <i className="fa fa-star-half" aria-hidden="true"/>
                }
            </div>
    );
};

export default Condition;



