import React, { Component } from 'react';
import PropTypes from 'prop-types';
import loadGoogleMapsAPI from 'load-google-maps-api';

const MAP_STYLES = {
    height: '450px',
    width: '100%'
};
const API_CONFIG = {
    key:'AIzaSyC8zUTwZ0EZyshaBrzBAWRhzeRixkBTTFI',
    language: 'es'
};

let garajeSale = '';

class Map extends Component {
    componentWillUnmount() {
        const allScripts = document.getElementsByTagName( 'script' );
        [].filter.call(
            allScripts,
            ( scpt ) => scpt.src.indexOf( 'key=AIzaSyC8zUTwZ0EZyshaBrzBAWRhzeRixkBTTFI' ) >= 0
        )[ 0 ].remove();
        window.google = {};
    }

    componentDidMount() {
        garajeSale = {
            center: this.props.coords,
            zoom: 16
        };
        this.loadGoogle();
    }

    loadGoogle() {
        loadGoogleMapsAPI( API_CONFIG ).then( googleMaps => {
            let map = new googleMaps.Map( this.refs.map, garajeSale );
            new googleMaps.Marker({
                position: garajeSale.center,
                map: map
            });
        }).catch( err => {
            console.warning( 'Something went wrong loading the map', err );
        });
    }

    render() {
        return (
            <div ref="map" style={MAP_STYLES}/>
        )
    }
}

Map.propTypes = {
    coords: PropTypes.object.isRequired
};

export default Map;


